.ajax-loader{
  font-size: 5px;
  display: table;
  background: none;
  top:100px;
  left: 0;
  right: 0;
  margin: auto;
  position: absolute;
  border-top: 0.78em solid #ffb036;
  border-right: 0.78em solid #ffb036;
  border-bottom: 0.78em solid #ffb036;
  border-left: 0.78em solid transparent;
  -webkit-animation: load 1s infinite linear;
  animation: load 1s infinite linear;
}
.ajax-loader,
.ajax-loader:after {
    border-radius: 50%;
    width: 10em;
    height: 10em;
}

@-webkit-keyframes load {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } 
}
@keyframes load {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } 
}


