.jcf-select {
  display: inline-block;
  vertical-align: top;
  position: relative;
  border: 1px solid #d9dfee;
  background: #fff;
  margin: 0;
  min-width: inherit;
  width: 255px;
  height: 46px;
  cursor: pointer;
  border-radius: 23px;
  .jcf-select-text {
    line-height: inherit;
    margin: 14px 35px 0 15px;
    font-size: 14px;
    color: #434f6a;
    letter-spacing: 0.01em;
    font-weight: 500;
    cursor: pointer;
  }
  .jcf-select-opener {
    position: absolute;
    text-align: center;
    background: none;
    width: 7px;
    height: 7px;
    border-right: 2px solid #2b364f;
    border-bottom: 2px solid #2b364f;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    transition: all .3s linear;
    bottom: auto;
    right: 20px;
    top: 17px;
    z-index: 9999;
  }
  &.jcf-drop-active{
    .jcf-select-opener{
      transform: rotate(-135deg);
    }
  }
}

