  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  margin-bottom: 70px;
  margin-top: 34px;
}

.pagination__item {
  margin: 0 8px;
  &.active .pagination__link {
    background: #2b364f;
    color: #fff;
  }
  &:hover .pagination__link{
    box-shadow: 0 2px 6px 0 rgba(#2b364f,0.2);
    &.pagination__link--dots{
      box-shadow:none;
    }
  }
}

@media only screen and (max-width: 480px) {
  .pagination__item {
    &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6) {
      display: none;
    }
  }
}

.pagination__link {
  width: 39px;
  height: 39px;
  border-radius: 50%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 14px;
  color: #000;
  background: #fff;
  transition: box-shadow .4s ease-in-out;
  &:hover{
    .pagination__link.pagination__link--dots{
      box-shadow:none;
    }
  }
}

.pagination__link--dots {
  width: auto;
  background: none;
  position: relative;
  top: 10px;
  margin: 0 -8px;
}

.pagination__link--prev {
  background: none;
}

.pagination__link--next {
  background: none;
  svg {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
}

.pagination__link svg {
  width: 12px;
  height: 14px;
  fill: #000;
}

.radio {
