  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.radio__label,.filter__form-social  li {
  display: block;
  width: 35px;
  height: 53px;
  margin-right: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: pointer;
}
.filter__form-social  li{
  position: relative;
  opacity: .4;
  -webkit-transition: opacity .2s ease-in-out;
  transition: opacity .2s ease-in-out;
  .icon{
    width: 35px;
    height: 35px;
    display: block;
    -webkit-transition: opacity .2s ease-in-out;
    transition: opacity .2s ease-in-out;
  }
  a{
    display: block;
    height: 100%;
    pointer-events:none;
    position: relative;
    width: 100%;
    &:after{
      content: "";
      -webkit-transition: height .2s ease-in-out;
      transition: height .2s ease-in-out;
      width: 100%;
      height: 0;
      bottom:0;
      left: 0;
      position: absolute;
    }
    &.facebook{
      .icon{
        background: url(../images/facebook.svg) no-repeat;
      }
      &:after{
        background: #3B5998;
      }
    }
    &.twitter{
      .icon{
        background: url(../images/twitter.svg) no-repeat;
      }
      &:after{
        background: #55ACEE;
      }
    }
    &.vkontakte{
      .icon{
        background: url(../images/vkontakte.svg) no-repeat;
      }
      &:after{
        background: #4D76A1;
      }
    }
    &.instagram{
      .icon{
        background: url(../images/instagram.svg) no-repeat;
      }
      &:after{
        background: #6A453B;
      }
    }
    &.youtube{
      .icon{
        background: url(../images/youtube.svg) no-repeat;
      }
      &:after{
          background: #c4302b;
      }
    }
    &:hover{
        opacity: 1;
    }
  }
  &.active{
    opacity: 1;
    .icon{
      opacity: 1;
    }
    a:after{
      height: 3px;
    }
  }
}

@media only screen and (min-width: 1025px) {
  .radio__label:hover svg {
    opacity: 1;
  }
}

.radio__input {
  display: none;
  &:focus ~ {
    img {
      opacity: 1;
    }
    .radio__line {
      height: 3px;
    }
  }
}
.filter__form-social li a{
    &:focus{
    img {
      opacity: 1;
    }
    .radio__line {
      height: 3px;
    }
  }
}

.filter__form-social li img {
  opacity: .3;
